import React, {useEffect, useState} from 'react'
import {List, Row, Col, Modal, message, Button} from 'antd'
import axios from 'axios'
import servicePath from '../config/apiUrl'
import '../static/css/ArticleList.css'
import { useNavigate } from 'react-router-dom';


const {confirm} = Modal;

function ArticleList(props){
    const navigate = useNavigate();

    const [list, setList] = useState([]);

    useEffect(()=>{
        getList();
    },[]);

    const getList = () => {
        console.log('ing: getList()');
        axios({
            method:'get',
            url: servicePath.getArticleList,
            withCredentials: true,  // session
        }).then(
            res=>{
                setList(res.data.list);
                console.log('res.data.list: ', res.data.list);
                console.log('res.data: ', res.data);
                console.log('res: ', res);
            }
        )
    }

    // 
    const delArticle =(id)=> {
        confirm({
            title: '',
            content: 'OK',
            onOk(){
                console.log('2222222');
                axios(servicePath.delArticle+id, {withCredentials:true}).then(
                    res=>{
                        message.success('');
                        getList();
                    }
                )
            },
            onCancel(){
                message.success(', ')
            }
        })
    }

    // AddArticle
    const updateArticle = (id) => {
        // navigate('/index/'+id);  // idAddArticle
        // navigate('/index/');  // idAddArticle
        navigate('/index/add/'+id);  // idAddArticle
    }

    return (
        <div>
            <List
                header={
                    <Row className='list-div'>
                        <Col span={8}>
                            <b></b>
                        </Col>
                        <Col span={4}>
                            <b></b>
                        </Col>
                        <Col span={4}>
                            <b></b>
                        </Col>
                        <Col span={4}>
                            <b></b>
                        </Col>
                        <Col span={4}>
                            <b></b>
                        </Col>
                    </Row>
                }
                bordered
                dataSource={list}
                renderItem={item=>(
                    <List.Item>
                        <Row className='list-div'>
                            <Col span={8}>
                                {item.title}
                            </Col>
                            <Col span={4}>
                                {item.typeName}
                            </Col>
                            <Col span={4}>
                                {item.addTime}
                            </Col>
                            <Col span={4}>
                                {item.view_count}
                            </Col>
                            <Col span={4}>
                                <Button type='primary' onClick={()=>{updateArticle(item.id)}} ></Button>
                                <Button onClick={()=>{delArticle(item.id)}} style={{marginLeft: '10px'}}></Button>
                            </Col>
                        </Row>
                    </List.Item>
                )}

            />

        </div>
    );
};

export default ArticleList;