import React, {useState, useEffect} from 'react'
import '../static/css/AddArticle.css'
import {Row, Col, Input, Select, Button, DatePicker, message} from 'antd'
import {marked} from "marked";
import axios from 'axios'
import servicePath from '../config/apiUrl'
import { useNavigate } from 'react-router-dom';
import { useParams } from 'react-router-dom';


const {Option} = Select;  // Select  Option
const {TextArea} = Input;  // 

function AddArticle(props) {
    const navigate = useNavigate();

    const [articleId,setArticleId] = useState(0)  // ID00
    const [articleTitle,setArticleTitle] = useState('')  //
    const [articleContent,setArticleContent] = useState('')  //markdown
    const [markdownContent,setMarkdownContent] = useState('')  //html
    const [introducemd,setIntroducemd] = useState()  //markdown
    const [introducehtml,setIntroducehtml] = useState('')  //html
    const [showDate,setShowDate] = useState()  //
    const [updateDate,setUpdateDate] = useState()  //
    const [typeInfo,setTypeInfo] = useState([])  // 
    const [selectedType,setSelectType] = useState('')  //


    useEffect(() => {
        console.log('loopinggggg');
        getTypeInfo();
        const tempId = 0; // props.match.params.id;
        // message.error('props = ', props);

        
        // const { tempId } = Number(useParams());

        // const tempId = props.match.params.id;
        if (tempId){
            setArticleId(tempId);
            getArticleById(tempId);
        }
    },[]);

    marked.setOptions({
        // renderer: marked.Renderer(),
        gfm: true,
        pedantic: false,
        sanitize: false,
        tables: true,
        breaks: true,
        smartLists: true,
        smartypants: false,
    })

    //  markdown content
    const changeContent = (e) => {
        setArticleContent(e.target.value);
        let html = marked(e.target.value);
        setMarkdownContent(html);
    }

    const changeIntroduce = (e) => {
        setIntroducemd(e.target.value);
        let html = marked(e.target.value);
        setIntroducehtml(html);
    }

    // type info
    const getTypeInfo = () => {
        console.log('getTypeInfo');
        // try {
        axios({
            method:'get',
            url: servicePath.getTypeInfo,
            withCredentials: true,  // 
            // credentials: true, // cookie
        }).then(
            res => {
                console.log("res.data.data ", res.data.data);
                if(res.data.data === ''){  // 
                    // sessionopenId(login)
                    localStorage.removeItem('openId');
                    navigate('/');
                }else{
                    setTypeInfo(res.data.data);
                }
            }
        )
        // } catch (error) {
        //     console.log('AddArticle axios error: ', error.response.data);
        // }
        
    }

    // 
    const selectTypeHandler = (value) => {
        setSelectType(value);  // 
    }

    // 
    const saveArticle = () => {
        if(!selectedType){
            message.error('');
            return false;
        } else if (!articleTitle) {
            message.error('');
            return false;
        } else if (!articleContent) {
            message.error('');
            return false;
        } else if (!introducemd) {
            message.error('');
            return false;
        } else if (!showDate) {
            message.error('');
            return false;
        }
        message.success('');
        let dataProps = {};
        dataProps.type_id = selectedType;
        dataProps.title = articleTitle;
        dataProps.article_content = articleContent;
        dataProps.intro = introducemd;

        let dataText = showDate.replace('-','/');
        dataProps.addTime = (new Date(dataText).getTime())/1000;  // 
        
        if (articleId === 0){  // 
            dataProps.view_count = 0;  // 
            message.info('');
            console.log('7777777');

            axios({
                method:'post',
                url:servicePath.addArticle,
                withCredentials: true,
                data: dataProps,
            }).then(
                res=>{
                    setArticleId(res.data.insertId);  // idid
                    if(res.data.isSuccess){
                        message.success('');
                    } else {
                        message.error('');
                    }
                }
            )
        } else {
            dataProps.id = articleId;
            axios({
                method: 'post',
                url:servicePath.updateArticle,
                data:dataProps,
                withCredentials:true,
            }).then(
                res=>{
                    if(res.data.isSuccess){
                        message.success('');
                    } else {
                        message.error('');
                    }
                }
            )
        }
    }

    // 
    const getArticleById=(id)=>{
        message.error('id = ', id);

        
        axios(servicePath.getArticleById+id,{
            withCredentials:true,
        }).then(
            res=>{
                const articleInfo = res.data.data[0];
                setArticleTitle(articleInfo.title)
                setArticleContent(articleInfo.article_content);
                const html = marked(articleInfo.article_content);
                setMarkdownContent(html);
                setIntroducemd(articleInfo.introduce);
                const tempIntro = marked(articleInfo.introduce);
                setIntroducehtml(tempIntro);
                setShowDate(articleInfo.addTime);
                setSelectType(articleInfo.type_id);

                
            }
        )
    }

    return (
        <div>
            <Row gutter={5}>
                <Col span={18}>
                    <Row gutter={10}>
                        <Col span={20}><Input placeholder='' size='large' value={articleTitle} onChange={(e)=>{setArticleTitle(e.target.value)}}/></Col>
                        <Col span={4}>
                            <Select defaultValue={selectedType} size='large' onChange={selectTypeHandler}>
                                {
                                    typeInfo.map((item,index)=>{
                                        return (<Option key={index} value={item.id}>{item.typeName}</Option>);
                                    })
                                }
                            </Select>
                        </Col>
                    </Row>
                    <br/>
                    <Row gutter={10}>
                        <Col span={12}>
                            <TextArea className='markdown-content' rows={35} value={articleContent} placeholder='' onChange={changeContent}/>
                        </Col>
                        <Col span={12}>
                            <div className='show-html' dangerouslySetInnerHTML={{__html:markdownContent}} ></div>
                        </Col>
                    </Row>

                </Col>
                <Col span={6}>
                    <Row>
                        <Col span={24}>
                            <Button size='large'></Button>
                            <Button type='primary' size='large' style={{margin: '0 16px'}} onClick={saveArticle} ></Button>
                        </Col>
                        <Col span={24}>
                            <TextArea rows={4} value={introducemd} placeholder='' onChange={changeIntroduce} style={{margin: '16px 0'}}></TextArea>
                            <div className='introduce-html' dangerouslySetInnerHTML={{__html:introducehtml}} ></div>
                        </Col>
                        <Col span={12} style={{margin: '16px 0'}}>
                            <div className='=date-select'>
                                <DatePicker placeholder='' size='large' onChange={(date, dateString)=>{setShowDate(dateString)}}/>
                            </div>
                        </Col>

                    </Row>
                </Col>
            </Row>
        </div>
    )
}
export default AddArticle;